@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
body {
  margin: 0;
  padding: 0;
  font-family: 'LotteMartDream', sans-serif, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-moz-selection { 
  background-color: #4e9660;
  color: #fff;
 }

::selection { 
  background-color: #4e9660;
  color: #fff;
 }
.App {
  text-align: center;
  background: url(/portfolio/static/media/tree-4041156_1920.35958242.jpg) 50% 0 no-repeat;
  background-attachment: fixed;
  margin-bottom: 220px;
  overflow: hidden;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.home {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
}

.home a.scroll {
  color: #8dbf99;
}

.home i.icon, .home i.icons {
  font-size: 2em;
  padding:10px;
}
.menu {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9;
}

.menu a.open_menu {
  color: #8dbf99;
  z-index: 9;
}

.menu i.icon, .menu i.icons {
  font-size: 2em;
  padding:10px 10px 0 0;
}

.menu ul {
  padding: 30px 0 0;
}

.menu ul li {
  list-style-type: none;
  font-size: 50px;
  font-weight: bold;
  line-height: 1.5;
}

.menu ul li a {
  color: #908b87;
}
.menu ul li a:hover {
  display: block;
  padding-top:3px;
  color: #90c39c;
}

.welcome {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: rgba(36,33,33,0.52);
  color: #fff;
  border: gray 1px solid;
}

.welcome .visual {
  position: absolute;
  top: 30%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.welcome .visual .main_title {
  font-size: 100px;
}


.show_delay {
  opacity: 0;
  -webkit-animation: show_title 4s ease-in forwards;
          animation: show_title 4s ease-in forwards;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}

.show_delayed_bounce {
  opacity: 0.6;
  -webkit-animation: bounce1 1.5s ease-in 100;
          animation: bounce1 1.5s ease-in 100;
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
}

.scroll_text {
  font-size: 15px;
}

.scroll_text p {
  margin-bottom: 0px;
}

@-webkit-keyframes show_title {
  0% {opacity:0;} 
  100% {opacity:1;} 

  /* opacity:1; */
}

@keyframes show_title {
  0% {opacity:0;} 
  100% {opacity:1;} 

  /* opacity:1; */
}

@-webkit-keyframes bounce1 {
  from,to {-webkit-transform:translateY(-7px);transform:translateY(-7px); -webkit-animation-timing-function:ease-in; animation-timing-function:ease-in;}
  50% {-webkit-transform:translateY(7px);transform:translateY(7px); -webkit-animation-timing-function:ease-out; animation-timing-function:ease-out;}
  }

@keyframes bounce1 {
  from,to {-webkit-transform:translateY(-7px);transform:translateY(-7px); -webkit-animation-timing-function:ease-in; animation-timing-function:ease-in;}
  50% {-webkit-transform:translateY(7px);transform:translateY(7px); -webkit-animation-timing-function:ease-out; animation-timing-function:ease-out;}
  }

/* Google Fonts */

.title_typewriter{
    position: relative;
    top: 50%;  
    width: 424px;
    margin: 0 auto;
    border-right: 2px solid rgba(255,255,255,.75);
    font-size: 21px;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-family: 'Anonymous Pro', monospace;  
}

/* Animation */
.show_typewriter{
  -webkit-animation: typewriter 4s steps(40) 1s 1 normal both,
             blinkTextCursor 500ms steps(40) infinite normal;
          animation: typewriter 4s steps(40) 1s 1 normal both,
             blinkTextCursor 500ms steps(40) infinite normal;
}
@-webkit-keyframes typewriter{
  from{width: 0;}
  to{width: 424px;}
}
@keyframes typewriter{
  from{width: 0;}
  to{width: 424px;}
}
@-webkit-keyframes blinkTextCursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
}
@keyframes blinkTextCursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
}

@media (max-width: 580px) {
  .welcome .visual .main_title {
    font-size: 55px;
  }
  .title_typewriter {
    width: 300px!important;
    font-size: 15px!important;
  }

  @-webkit-keyframes typewriter{
      from{width: 0;}
      to{width: 300px!important;}
    }

  @keyframes typewriter{
      from{width: 0;}
      to{width: 300px!important;}
    }
}
.info {
  background: #fdf7f3;
  margin-bottom: 3em;
}

.info span.sub_tit {
  display: block;
  font-size: 50px;
  font-weight: bold;
  padding: 2em 0px 20px;
  color: #8dbf99;
  position: relative;
}

.info .sub_tit:after {
  background: #8dbf99;
  border-radius: .25em;
  display: block;
  width: 1.25em;
  height: 2px;
  margin-left: -.625em;
  position: absolute;
  top: 40%;
  left: 50%;
  content: "";
}

.info i {
  font-style: normal;
  color: #cfdccb;
}
.info p{
  padding: 4em 5em 8em;
  font-size: 17px;
  letter-spacing: 0.025em;
  line-height: 2;
  text-align: center;
  font-weight: 400;
}

.info p strong {
  color: #ff5600;
}

@media (max-width: 580px) {
  .info p{ 
    text-align: left;
    font-size: 15px;
   }
}
.worklist {
  background:#46362a;
  color: #fff;
  padding-bottom: 5em;
  box-shadow: 0px 10px 40px #46362a;
  min-height: 100vh;
}


.category-label { 
  width: 60px;
}

.worklist span.sub_tit {
  display: block;
  font-size: 44px;
  font-weight: bold;
  padding: 2em 0px 20px;
  color: #8dbf99;
  position: relative;
}

.worklist .sub_tit:after {
  background: #8dbf99;
  border-radius: .25em;
  display: block;
  width: 1.25em;
  height: 2px;
  margin-left: -.625em;
  position: absolute;
  top: 40%;
  left: 50%;
  content: "";
}

.work::after {
  content: "";
  display: block;
  clear: both;
}
.work h4 {
  margin: 2em 0;
  font-size: 28px;
}
.work .work_img{ 
  display: inline-block;
  width: 50%;
  margin: 5%;
  float: left;
}
.work .work_img img{
  width: 100%;
}

.work .work_desc {
  display: inline-block;
  width: 30%;
  margin: 5%;
  float: left;
  text-align: left;
}

.work .work_desc ul {
  padding: 0;
}
.work .work_desc ul li{
  list-style: none;
  padding: 0;
}

.work .work_desc ul li strong{
  display: inline-block;
  width: 100px;
}

.work .work_desc p {
  margin: 3em 0;
}

.btn_work_wrap {
  display: inline-block;
  width: 100%;
  text-align: center;
}
.work_table {
  display: block;
  width:100%;
}

.work_table table, .ui.table thead th  {
  color: #fff!important;
  border-color: #e0e1e2;
  background: #54473f;
  /* padding:10em; */
}

.work_table table {
  border-top: 1px solid #fff;
  border-bottom: 1px solid;
  padding: 10px 0;
  border-radius: 0;
}


@media (max-width: 760px) {
  .work .work_img, .work_table { 
    width: 80%;
      margin: 0 10%;
   }
   .work .work_desc {

    width: 80%;
     margin: 1em 10% 4em;
   }
}
.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height:220px;
  padding: 2em 4em;
  background:#fdf7f3;
  text-align: left;
  z-index: -1;
}

.footer h2 {
  text-align: center;
}

.footer .box_shadow {
  width: 100%;
  box-shadow: 0 10px 60px rgba(20,20,20,0.1), 0 10px 50px rgba(30,30,30,0.06) inset;
}

.footer ul {
  padding: 0;
  list-style-type: none;
}

.footer ul li {
  display: block;
}

.footer ul li a{
  display: inline-block;
  color: #463628;
  padding: 6px 0;
  font-size: 16px;
}

.footer_bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  color: #fff;
  text-align: right;
  padding: 10px;
  background: #908b86;
}

@media (min-width:640px) {
  .footer {
    padding-top: 3em;
  }
  .footer ul {
    text-align: center;
  }
  .footer ul li {
    display: inline-block;
    padding: 1em;
  }
}
